/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React from "react"
import PropTypes from "prop-types"
import { useStaticQuery, graphql, Link } from "gatsby"
import col_checklist from "../images/pdf/College_Forms/College_Checklist.pdf"
import col_reference from "../images/pdf/College_Forms/College_Reference_Form.pdf"
import col_transcript from "../images/pdf/College_Forms/College_Transcript_Form.pdf"
import col_employee from "../images/pdf/College_Forms/Employee_Verification_Form.pdf"
import col_release from "../images/pdf/College_Forms/Release_Form.pdf"
import hs_employee from "../images/pdf/HS_Forms/Employee_Verification_Form.pdf"
import hs_checklist from "../images/pdf/HS_Forms/HighSchool_Checklist.pdf"
import hs_reference from "../images/pdf/HS_Forms/HighSchool_Reference_Form.pdf"
import hs_transcript from "../images/pdf/HS_Forms/HS_Transcript_Form.pdf"
import hs_release from "../images/pdf/HS_Forms/Release_Form.pdf"

import Header from "./header"
import "./layout.css"

const Layout = ({ children }) => {
  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)
  const [isMenuOpen, setIsMenuOpen] = React.useState(false)

  return (
    <div className="layout">
      <Header siteTitle={data.site.siteMetadata.title} onMenuClick={() => setIsMenuOpen(!isMenuOpen)} />
      <div>
        <div className={isMenuOpen ? 'menu menu--active' : 'menu'}>
          <span>Current High School Seniors</span>
          <a target="_blank" href={col_checklist}>Check List</a>
          <a target="_blank" href={col_transcript}>Transcript Form</a>
          <a target="_blank" href={col_employee}>Employee Verificatin Form</a>
          <a target="_blank" href={col_reference}>Reference Form</a>
          <a target="_blank" href={col_release}>Release Form</a>
          <span>Current College Students</span>
          <a target="_blank" href={hs_checklist}>Check List</a>
          <a target="_blank" href={hs_transcript}>Transcript Form</a>
          <a target="_blank" href={hs_employee}>Employee Verificatin Form</a>
          <a target="_blank" href={hs_reference}>Reference Form</a>
          <a target="_blank" href={hs_release}>Release Form</a>
        </div>
        <main className="main">{children}</main>
        <footer className="footer">
          <Link to={'/terms'}>Terms of Use</Link>
          <Link to={'/privacy'}>Privacy Policy</Link>
        </footer>
      </div>
    </div>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
